<template>
  <div
    v-loading.fullscreen.lock="loading"
    element-loading-text="拼命加载中"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)"
  >
    <el-dialog
      :visible.sync="getshow"
      width="60%"
      center
      :fullscreen="showFullDlg"
      :custom-class="''"
    >
      <el-row>
        <el-col :lg="8" :xs="8">
          <v-img
            id="Avatar_dlg"
            :src="`img/avatar/${gundam.ID}.gif`"
            :lazy-src="`img/avatar/${gundam.ID}.gif`"
            aspect-ratio="1"
            class="grey lighten-2"
            max-width="120"
          >
            <el-col slot="placeholder" fill-height align-center justify-center ma-0>
              <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
            </el-col>
          </v-img>
        </el-col>
        <el-col :lg="6" :xs="14">
          <h2 class="h2">{{gundam.Name || gundam.model}}</h2>
          <div>
            Rank:
            <strong class="h3">{{gundam.rank}}</strong>
          </div>
          <div>{{gundam.landType}} {{gundam.fightType}}</div>
        </el-col>
        <el-col :lg="10">
          <div>梦服状态：{{newState}}</div>
          <div>登场时间：{{gundam.birth_date || '未知'}}</div>
          <br>
          <div v-if="false">稀有度：{{gundam.rare}}</div>
          <div v-if="false">强度：{{gundam.strength}}</div>
          <br>
          <div v-if="gundam.get_way">获取途径：{{gundam.get_way}}</div>
          <div v-if="gundam.GB_sale">GB价格：{{gundam.GB_sale}}</div>
          <div v-if="gundam.MB_sale">MB价格：{{gundam.MB_sale}}</div>
          <div v-if="gundam.map_sale">图纸价格：{{gundam.map_sale}}</div>
        </el-col>
      </el-row>
      <br>
      <el-row>
        <el-col :lg="12" :xs="24">
          <table width="90%">
            <tbody>
              <tr>
                <td colspan="6" align="center" bgcolor="#FFFFFF">机体数值</td>
              </tr>
              <tr>
                <td bgcolor="#666699">
                  <font color="#FFFFFF">攻击</font>
                </td>
                <td bgcolor="#666699">
                  <font color="#FFFFFF">防御</font>
                </td>
                <td bgcolor="#666699">
                  <font color="#FFFFFF">机动</font>
                </td>
                <td bgcolor="#666699">
                  <font color="#FFFFFF">操控</font>
                </td>
                <td bgcolor="#666699">
                  <font color="#FFFFFF">4D综合</font>
                </td>
                <td bgcolor="#666699">
                  <font color="#FFFFFF">3D综合</font>
                </td>
              </tr>
              <tr>
                <td bgcolor="#FFFFFF">{{gundam.att}}</td>
                <td bgcolor="#FFFFFF">{{gundam.def}}</td>
                <td bgcolor="#FFFFFF">{{gundam.fly}}</td>
                <td bgcolor="#FFFFFF">{{gundam.ctl}}</td>
                <td bgcolor="#FFFFFF">{{gundam.D4sum}}</td>
                <td bgcolor="#FFFFFF">{{gundam.D3sum}}</td>
              </tr>
            </tbody>
          </table>
        </el-col>
        <el-col :lg="12" :xs="24">
          <table>
            <tbody>
              <tr>
                <td bgcolor="#666699" style="width:120px">
                  <font color="#FFFFFF">繁体名称</font>
                </td>
                <td bgcolor="#666699">
                  <font color="#FFFFFF">冲刺时间</font>
                </td>
                <td bgcolor="#666699">
                  <font color="#FFFFFF">跳跃时间</font>
                </td>
                <td bgcolor="#666699">
                  <font color="#FFFFFF">索敌距离</font>
                </td>
                <!-- <td bgcolor="#666699">
                  <font color="#FFFFFF"> </font>
                </td>
                <td bgcolor="#666699">
                  <font color="#FFFFFF"> </font>
                </td>-->
              </tr>
              <tr>
                <td bgcolor="#FFFFFF">{{gundam.nameCN_G}}</td>
                <td bgcolor="#FFFFFF">{{gundam.rushTime}}</td>
                <td bgcolor="#FFFFFF">{{gundam.jumpTime}}</td>
                <td bgcolor="#FFFFFF">{{gundam.Radar_distance}}</td>
              </tr>
            </tbody>
          </table>
        </el-col>
      </el-row>

      <el-row>
        <!-- weapon -->
        <el-col :lg="12" :xs="24">
          <table v-html="gundam.weaponTable"></table>
        </el-col>
        <!-- skill -->
        <el-col :lg="12" :xs="24">
          <el-table :data="gundam.skill" :show-header="false">
            <el-table-column prop="skill_name" label="skill_name">
              <template slot-scope="scope">
                <!-- <img :src="scope.row.img_local" width="30"> -->
                <v-img
                  :src="`img/skill/${scope.row.img_local.match(/\d+\.gif/)}`"
                  :lazy-src="`img/skill/${scope.row.img_local.match(/\d+\.gif/)}`"
                  aspect-ratio="1"
                  max-width="30"
                ></v-img>
                <strong>{{scope.row.skill_name}}</strong>
                <br>
                <span>{{scope.row.skill_detail}}</span>
              </template>
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>

      <!-- 合成需要 -->
      <p v-if="gundam.makeNeed&&gundam.makeNeed.length">合成需要：</p>
      <el-row>
        <el-col>
          <el-card v-for="(robots) in gundam.makeNeed" :key="robots.ID" class="mk-card">
            <a :title="robots.name" href="#Avatar_dlg" @click="updateGundam(robots)">
              <v-img
                :src="`img/avatar/${robots.id}.gif`"
                :lazy-src="`img/avatar/${robots.id}.gif`"
                aspect-ratio="1"
                :class="robots.role&&robots.role == 'key'? 'as-key':''"
                max-width="60"
              ></v-img>
              <br>
              <span class="mkcard-word">{{robots.name}}：{{robots.Level}}</span>
            </a>
          </el-card>
        </el-col>
      </el-row>
      <!-- 国服合成需要makeNeed_CN -->
      <p v-if="gundam.makeNeed_CN&&gundam.makeNeed_CN.length">国服合成需要：</p>
      <el-row>
        <div>
          <el-card v-for="(robots) in gundam.makeNeed_CN" :key="robots.ID" class="mk-card">
            <a :title="robots.name" href="#Avatar_dlg" @click="updateGundam(robots)">
              <v-img
                :src="`img/avatar/${robots.id}.gif`"
                :lazy-src="`img/avatar/${robots.id}.gif`"
                aspect-ratio="1"
                :class="robots.role&&robots.role == 'key'? 'as-key':''"
                max-width="60"
              ></v-img>
              <br>
              <span class="mkcard-word">{{robots.name}}：{{robots.Level}}</span>
            </a>
          </el-card>
        </div>
      </el-row>
      <!-- 用于合成 -->
      <p v-if="gundam.toMake&&gundam.toMake.length">用于合成：</p>
      <el-row v-if="gundam.toMake&&gundam.toMake.length">
        <div>
          <el-card v-for="(robots) in gundam.toMake" :key="robots.ID" class="mk-card">
            <a :title="robots.name" href="#Avatar_dlg" @click="updateGundam(robots)">
              <v-img
                :src="`img/avatar/${robots.id}.gif`"
                :lazy-src="`img/avatar/${robots.id}.gif`"
                aspect-ratio="1"
                :class="robots.role&&robots.role == 'key'? 'as-key':''"
                max-width="60"
              ></v-img>
              <br>
              <span class="mkcard-word">{{robots.name}}：{{robots.Level}}</span>
            </a>
          </el-card>
        </div>
      </el-row>

      <!-- 扭蛋 -->
      <el-row>
        <span v-if="gundam.Machine&&gundam.Machine.length">扭蛋机：</span>
        <el-tag v-for="(Num, index) in gundam.Machine" :key="index">{{Num}}</el-tag>
      </el-row>
      <!-- 更多信息 -->
      <el-row>
        <p>其他信息：</p>
        <p>-势力：{{gundam.force}}</p>
        <p>-{{gundam.feature}}</p>
        <p>-出自作品：{{gundam.from}}</p>
        <p>-驾驶员：{{gundam.pilot}}</p>
        <p>{{gundam.story}}</p>
      </el-row>
      <el-button type="primary" @click="getshow = false">关闭详情</el-button>

      <!-- 内层dialog，用于读取数据加载间隙 -->
      <!-- <el-dialog
        width="30%"
        title="内层 Dialog"
        :visible.sync="innerVisible"
        v-loading.fullscreen.lock="loading"
      >请稍后</el-dialog>-->
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      loading: false,
      fullscreenLoading: false
    };
  },
  computed: {
    getshow: {
      get: function() {
        return this.show || false;
      },
      set: function() {
        this.$emit("dialogData", false);
      }
    },
    showFullDlg: {
      get: function() {
        let ScreenWidth = document.body.clientWidth;
        return ScreenWidth < 768 ? true : false;
      },
      set: function(val) {
        return val;
      }
    },
    newState: {
      get: function() {
        if (this.gundam.newState) return this.gundam.newState;
        if (this.gundam.get_way) return "已登场";
        return "未知";
      }
    }
  },
  props: {
    show: Boolean,
    gundam: Object
  },
  updated: function() {
    var div = document.querySelector(".el-dialog__wrapper");
    // div.scrollTop = div.scrollHeight;
    div.scrollTop = 0;
  },
  methods: {
    updateGundam: function(robot) {
      // this.$emit("dialogData", robot);
      this.gundam.AvatarUrl_TW = "img/loading.gif";
      this.loading = true;
      axios.post("/getGundam/findOne", robot).then(res => {
        // res.data.weaponTable = res.data.weaponTable.replace(
        //   /https\:\/\/www.olgame\.tw\/sds\/images\/weapon\/weapon_/g,
        //   "http://cdn.sdplayer.club/weapon/"
        // );
        this.gundam = res.data;
        this.loading = false;
        var div = document.querySelector(".el-dialog__wrapper");
        // div.scrollTop = div.scrollHeight;
        div.scrollTop = 0;
      });
    }
  }
};
</script>

<style>
.img-responsive {
  max-width: 100%;
  height: 110px;
  padding-bottom: 20%;
  overflow: hidden;
}
.img-responsive img {
  width: 42%;
}
.img-responsive-sub {
  max-width: 100%;
  width: 80px;
  height: 100px;
  padding-bottom: 10%;
  overflow: hidden;
  margin-right: 20px;
}
.img-responsive-sub img {
  width: 50%;
}
.as-key {
  background: palevioletred;
}
.mk-card {
  width: 150px;
  height: 130px;
  position: relative;
  float: left;
}
.mkcard-word {
  font-size: 8px;
}
.dlg-body {
  background-color: #f0f4c3 !important;
}
</style>
